'use scrict';
import { debounce } from '@/utils/util.js';

export default {
	data() {
		return {
			isShow: false
		}
	},
	computed: {
		handHide() {
			return debounce(() => {this.isShow = false}, 2000);
		}
	},
	methods: {
		show() {
			this.isShow = true;
			this.handHide();
		}
	}
}
